$royalBlue: #111a41;
$carnation: #fb6164;
$shamRock: hsl(143, 62%, 47%);
$bunting: #111a41;
$stormGray: #6c6e84;
$lightGray: #eee;
$portage: #8491e6;
$blueGroto: #0476D0;

@import '~material-icons/iconfont/material-icons.css';
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');

@mixin useFlex($direction, $justify, $align) {
	display:flex;
	flex-direction: $direction;
	align-items: $align;
}

@mixin grayBorder(){
	border: 1px solid $lightGray;
	border-radius: 3px;
}

:root {
	font-size: 62.5%;
}

body {
	// font-family: "Raleway", "HelveticaNeue", "Helvetica Neue", Helvetica, Arial, sans-serif;
	font-family: 'Lato', sans-serif;
	padding: 0;
	margin: 0;
}

#root {
  @include useFlex(column, center, center);
  justify-content: center;
}

main {
	@include useFlex(column, none, center);
	padding: 5px 10px;
}

header {
	width: 100%;
    max-width: 640px;
    display: flex;
    justify-content: space-between;
	align-items: center;
    padding: 10px 0px;
    margin-bottom: 15px;
}

header .resume-header-link {
	text-decoration: none;
	display: flex;
	color: $blueGroto;
	svg {
		width: 3.5rem;
		height: auto;
	}

	span {
		font-size: 3rem;
		font-weight: 600;
		
	}
}

header .preview  {
	text-decoration: none;
    font-size: 2.1rem;
    padding: 2px 5px;
    border-radius: 3px;
    font-weight: 600;;
}


.cv-form {
	@include useFlex(column, none , none);
	max-width: 600px;
	min-height: 90vh;
	border-radius: 10px;
	padding: 16px 20px;
	box-shadow: 4px 4px 24px rgba(139, 140, 165, 0.5);
	margin-bottom: 50px;
	.form-section {
		position: relative;

		label {
			min-width: 250px;
			font-size: 2.1rem;
			margin-top: 15px;
			margin-bottom: 3px;
			margin-right: 8px;
			font-weight: 500;
		}

		.form-item {
			@include useFlex(column, unset , unset);
			@include grayBorder();		
			flex-wrap: wrap;
			flex-direction: row;
			justify-content: space-between;
			margin-bottom: 7px;
			padding: 5px 20px 5px;
			position: relative;
		}
		input, textarea{
			outline: none;
			border: none;
			margin-bottom: 2px;
			background-color: $lightGray;
			padding: 8px;
			border-radius: 3px;

			&:valid {
				border: 1px solid $shamRock;
			}
		}
	}
}

button {
	width: 140px;
	display: block;
	text-align: center;
	margin: 10px auto ;
	font-size: 17px;
	outline: none;
	border: none;
	border-radius: 3px ;
	padding: 4px 3px;
	font-weight: 600;
	cursor: pointer;
	background-color: white;
	border: 1px solid $blueGroto;
	color: $blueGroto;
	transition: all 0.2s ease-in 0s;
	&:hover {
		background-color: $blueGroto;
		color: white;
	}
}

.green-button {
		color: $shamRock;
		background-color: white;
		border: 2px solid $shamRock;
		&:hover {
			color: white;
			background-color: $shamRock;
		}
}

.trash-can {
	position: absolute;
	right: 10px;
	cursor: pointer;
	transition: all 0.1s ease-in 0s;
	&:hover {
		color: #F83839;
	}
}

.buttons-div {
	display: flex;
    justify-content: space-evenly;
    margin: auto;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: center;
}

.textarea-div {
	width: 100%;
}
.text-area {
	min-height: 70px;
}

.worked-days {
	width: 100% ;
	display:flex;
	justify-content: space-between;
	flex-wrap: wrap;
}
.internal-form-section {
	@include useFlex(column, none, none);
}

.section-div-inline {
	display: flex;
	flex-direction: column;
	min-width: 230px;
	transition: all 0.2s ease-in 0s;
}

.CV {
	  min-width: 600px;
		min-height: 850px;
    max-width: 900px;
    padding: 20px;
    box-shadow: 1px 1px 8px rgba(139, 140, 165, 0.5);
    margin-top: 40px;
}

.basic-info {
	&:after {
		content: '';
		margin-top: 5px;
		width: 98%;
		height:2px;
		background-color: black;
    display: block;
	}
	div {
		font-size: 1.8rem;
		margin: 0px;
		padding: 1px;

		h2 {
			margin:6px 0px;
		}
		p {
			margin: 0px 10px
		}
	}
}

.courses, .experiences {
	font-size: 1.6rem;
	h3 {
		margin: 10px 0px;
	}
	.section-div {
		display: flex;
		flex-direction: column;
		div {
			display: flex;
			flex-direction: row;
			align-items: center;
			h4 {
				margin: 10px;
			}
			p {
				margin: 0;
			}
			.dates {
				margin-right: 25px;
			}
		}
	}
}

footer {
	width: 100%;
}

footer a {
	padding: 5px;
    text-decoration: none;
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
	span {
		color: white;
		font-size: 1.6rem;
		margin-left: 6px;
	}
}
