@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
:root{font-size:62.5%}body{font-family:'Lato', sans-serif;padding:0;margin:0}#root{display:flex;flex-direction:column;align-items:center;justify-content:center}main{display:flex;flex-direction:column;align-items:center;padding:5px 10px}header{width:100%;max-width:640px;display:flex;justify-content:space-between;align-items:center;padding:10px 0px;margin-bottom:15px}header .resume-header-link{text-decoration:none;display:flex;color:#0476D0}header .resume-header-link svg{width:3.5rem;height:auto}header .resume-header-link span{font-size:3rem;font-weight:600}header .preview{text-decoration:none;font-size:2.1rem;padding:2px 5px;border-radius:3px;font-weight:600}.cv-form{display:flex;flex-direction:column;align-items:none;max-width:600px;min-height:90vh;border-radius:10px;padding:16px 20px;box-shadow:4px 4px 24px rgba(139,140,165,0.5);margin-bottom:50px}.cv-form .form-section{position:relative}.cv-form .form-section label{min-width:250px;font-size:2.1rem;margin-top:15px;margin-bottom:3px;margin-right:8px;font-weight:500}.cv-form .form-section .form-item{display:flex;flex-direction:column;align-items:unset;border:1px solid #eee;border-radius:3px;flex-wrap:wrap;flex-direction:row;justify-content:space-between;margin-bottom:7px;padding:5px 20px 5px;position:relative}.cv-form .form-section input,.cv-form .form-section textarea{outline:none;border:none;margin-bottom:2px;background-color:#eee;padding:8px;border-radius:3px}.cv-form .form-section input:valid,.cv-form .form-section textarea:valid{border:1px solid #2ec267}button{width:140px;display:block;text-align:center;margin:10px auto;font-size:17px;outline:none;border:none;border-radius:3px;padding:4px 3px;font-weight:600;cursor:pointer;background-color:white;border:1px solid #0476D0;color:#0476D0;transition:all 0.2s ease-in 0s}button:hover{background-color:#0476D0;color:white}.green-button{color:#2ec267;background-color:white;border:2px solid #2ec267}.green-button:hover{color:white;background-color:#2ec267}.trash-can{position:absolute;right:10px;cursor:pointer;transition:all 0.1s ease-in 0s}.trash-can:hover{color:#F83839}.buttons-div{display:flex;justify-content:space-evenly;margin:auto;flex-direction:row;flex-wrap:wrap;align-content:center}.textarea-div{width:100%}.text-area{min-height:70px}.worked-days{width:100%;display:flex;justify-content:space-between;flex-wrap:wrap}.internal-form-section{display:flex;flex-direction:column;align-items:none}.section-div-inline{display:flex;flex-direction:column;min-width:230px;transition:all 0.2s ease-in 0s}.CV{min-width:600px;min-height:850px;max-width:900px;padding:20px;box-shadow:1px 1px 8px rgba(139,140,165,0.5);margin-top:40px}.basic-info:after{content:'';margin-top:5px;width:98%;height:2px;background-color:black;display:block}.basic-info div{font-size:1.8rem;margin:0px;padding:1px}.basic-info div h2{margin:6px 0px}.basic-info div p{margin:0px 10px}.courses,.experiences{font-size:1.6rem}.courses h3,.experiences h3{margin:10px 0px}.courses .section-div,.experiences .section-div{display:flex;flex-direction:column}.courses .section-div div,.experiences .section-div div{display:flex;flex-direction:row;align-items:center}.courses .section-div div h4,.experiences .section-div div h4{margin:10px}.courses .section-div div p,.experiences .section-div div p{margin:0}.courses .section-div div .dates,.experiences .section-div div .dates{margin-right:25px}footer{width:100%}footer a{padding:5px;text-decoration:none;background-color:black;color:white;display:flex;align-items:center;justify-content:center}footer a span{color:white;font-size:1.6rem;margin-left:6px}

